Link{

    color: #FAFAFA;
}
nav {

    font-family: 'Alata', sans-serif;
    
}

.nav-styles {
    color: #FAFAFA!important;

}

.nav-styles:link {
    text-shadow: 1px 1px 0 rgb(2, 48, 51);
    text-decoration: none;
    color: #FAFAFA;
}


.nav-styles:hover {
    background-color: #FAFAFA!important;
    color: rgb(6, 132, 141)!important;
}

.nav-styles:active {
    background-color: #FAFAFA!important;
    color: rgb(6, 132, 141)!important;
}

.nav-styles:visited {
    background-color: #FAFAFA!important;
    color: rgb(6, 132, 141);
}


.styled-nav {
    background-color: rgb(6, 132, 141);
    
}

li{

    color: #FAFAFA;
}
#myElement {

    color: #16f1e7;
    text-align: center;
    font-size: 2em;
    text-shadow: 10px 10px 10px rgb(22, 19, 19);
    padding-top: 15%;
    font-family: 'Alata', sans-serif;
}

main {

    height: 60vh;
    background-image: url(/static/media/cecil.ea603354.jpg);
    background-size: cover;
    border-radius: 1em;

}

#wrapper {

    background-color: #000;
    opacity: .7;
    height: 100%;
    width: 100%;
    border-radius: 1em;
    z-index: -1;
}

#about-btn {

    margin-top: 5%;
    text-align: center;
    position: absolute;
    top: 30%;
    left: 50%;
    background-color: #16f1e7;
    font-family: 'Alata', sans-serif;

}




@media (max-width: 576px) {

main {

    height: 70vh;
