#project-display{

    background-image: url(/static/media/time.c92ab423.jpg);
    background-size: contain;
    background-repeat: no-repeat;
    height: 100%;
    padding: 20%;
}


#project-content {

 
    padding: 10px;
    opacity: .9;
    border-radius: 2em;
    position: relative;
    left: 37%;
}

#project {
    position: relative;
    left: 30%;
}

@media(max-width: 1026px){

    #project-display{

        background-image: none;
        background-size: contain;
        background-repeat: no-repeat;
        height: 100%;
        padding: 20%;
    }

    #project {
        position: relative;
        left: 5%;
    }


    #project-content {

 
        padding: 10px;
        opacity: .9;
        border-radius: 2em;
        position: relative;
        left: 5%;
    }
}


